.modal {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(255, 255, 255, 0.3);
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
  }
  
  .modalShow {
    display: block;
  }
  
  .modalHide {
    display: none;
  }
  
  .modalContent {
    position: inherit;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #fff;
    color: #222;
    padding: 2rem;
    width: -webkit-max-content;
    width: max-content;
    box-shadow: 0px 0px 15px 0px #ccc;
  }
  
  .modalClose {
    position: absolute;
    right: -1.5rem;
    top: -1.5rem;
    height: 3rem;
    width: 3rem;
    font-size: 2.5rem;
    border-radius: 50%;
    background-color: #555;
    color: #fff;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    border: none;
    float: right;
    font-weight: light;
    cursor: pointer;
  }
  
  .modalClose:focus,
  .modalClose:hover {
    outline: none;
    background-color: #222;
  }
  
  .modalContent h2 {
    margin-top: 2rem;
  }
  
  .modalContent h3 {
    font-size: 2rem;
    text-shadow: 2px 2px 2px rgb(138, 57, 231);
  }
  
  .modalBalance {
    background-color: rgb(213, 60, 233);
    text-align: center;
    margin-top: 0.5rem;
    padding: 0.5rem;
  }
  
  .modalBalance h3 {
    font-weight: 700;
    font-size: 2rem;
  }
  
  .transactionFrom {
    border: 1px solid black;
    padding: 1rem;
    font-weight: 600;
  }
  
  .transactionFrom label {
    padding-right: 0.5rem;
  }
  
  .transactionFrom input,
  .transactionFrom select {
    background-color: #ddd;
    padding: 0.5rem 1rem;
    margin-bottom: 0.5rem;
    border: none;
  }
  
  .transactionFrom button {
    padding: 0.5rem 1rem;
    margin-top: 0.5rem;
    background-color: #222;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 0.2rem;
    font-size: 1rem;
  }
